import React from "react";
import { Button, Alert } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import { Terminal } from "react-window-ui";
import ReactJson from "react-json-view";

const JsonTerminal = props => {
  return (
    <div>
      <br/>
      <center>
        <p>
          <a target="_blank" href={props.generate_json_link} download rel="noopener noreferrer">
            <Button color="warning">View Raw .json Output</Button>
          </a>
        </p>
      </center>
      <Terminal>
        <Alert color="primary">.json Output</Alert>
        <ReactJson
          src={props.output_json_data}
          theme="monokai"
          style={{ textAlign: "left" }}
        />
      </Terminal>
    </div>
  );
};

export default JsonTerminal;
