import React, { Component } from "react";
import axios from "axios";
import "../css/media.css";
import { Container, Row, Col } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import MediaCarousel from "./../../media-carousel/scripts/media_carousel.js";
import FooterSessionButtons from "./../../footer-session-buttons/scripts/footer_session_buttons.js";
import TextTerminal from "./../../text-terminal/scripts/text_terminal.js";
import JsonTerminal from "./../../json-terminal/scripts/json_terminal.js";
import AlertWithSharing from "./../../alert-with-share/scripts/alert_with_share.js";
import AlertWithBadge from "./../../alert-with-badge/scripts/alert_with_badge.js";

var self;

class DisplayResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      _session: props.session,
      get_json_link:
        "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/getLinks.php?session_folder_name=",
      json_data_media: [],
      output_text_file_link:
        "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/uploads/" +
        props.session +
        "/out/" +
        props.session +
        ".txt",
      generate_json_link:
        "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/generateJSON.php?session_folder_name=" +
        props.session,
      generate_text_link:
        "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/getTextOutput.php?session_folder_name=" +
        props.session,
      json_output_data: [],
      text_output_data: [],
      share_link: "http://nirbhay.me/pothole-detection/#/display/" + props.session,
      from_shared_link: false,
      add_more_link: "http://nirbhay.me/pothole-detection/#/"
    };
    if (!this.state._session) {
      this.state = {
        _session: this.props.match.params.id,
        get_json_link:
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/getLinks.php?session_folder_name=",
        json_data_media: [],
        output_text_file_link:
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/uploads/" +
          this.props.match.params.id +
          "/out/" +
          this.props.match.params.id +
          ".txt",
        generate_json_link:
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/generateJSON.php?session_folder_name=" +
          this.props.match.params.id,
        generate_text_link:
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/getTextOutput.php?session_folder_name=" +
          this.props.match.params.id,
        json_output_data: [],
        text_output_data: [],
        share_link:
          "http://nirbhay.me/pothole-detection/#/display/" + this.props.match.params.id,
        from_shared_link: true,
        add_more_link: "http://nirbhay.me/pothole-detection/#/" + this.props.match.params.id
      };
    }

    self = this;
    axios
      .get(this.state.get_json_link + this.state._session)
      .then(function(response) {
        self.setState({
          json_data_media: response.data
        });
      });
    axios.get(this.state.generate_text_link).then(function(response) {
      self.setState({
        text_output_data: response.data
      });
    });
    axios.get(this.state.generate_json_link).then(function(response) {
      self.setState({
        json_output_data: response.data
      });
    });
  }

  checkForContinueSessionButtons() {
    if (this.state.json_data_media) {
      if (this.state.json_data_media.length > 0) {
        if (this.state.from_shared_link) {
          return (
            <div>
              <FooterSessionButtons
                button_text={"Start Afresh"}
                add_more_link={"http://nirbhay.me/pothole-detection/#"}
              />
              <FooterSessionButtons
                button_text={"Add some other image(s)/video(s)"}
                add_more_link={this.add_more_link}
              />
            </div>
          );
        }
      }
    }
  }

  checkAndGetOutput() {
    if (this.state.json_data_media) {
      if (this.state.json_data_media.length > 0) {
        return (
          <div>
            <Container>
              <AlertWithSharing
                session_no={this.state._session}
                link={this.state.share_link}
                message={
                  "Hey, view my test media for potholes detected! You can add your own to it too! Happy detecting and spreading the word!!"
                }
              />
            </Container>

            <Container>
              <MediaCarousel mediaData={this.state.json_data_media} />
            </Container>

            <Container>
              <Row>
                <Col xs="12" sm="6">
                  <TextTerminal
                    output_text_link={this.state.output_text_file_link}
                    output_text_data={this.state.text_output_data}
                  />
                </Col>

                <Col sm="6">
                  <JsonTerminal
                    generate_json_link={this.state.generate_json_link}
                    output_json_data={this.state.json_output_data}
                  />
                </Col>
              </Row>
            </Container>
          </div>
        );
      } else {
        return (
          <div>
            <br />
            <Container>
              <center>
                <AlertWithBadge
                  session_no={this.state._session}
                  color={"danger"}
                  alert_message={"Session Number Does Not Exist!!"}
                />
                <FooterSessionButtons
                  button_text={"Start Afresh"}
                  add_more_link={"http://nirbhay.me/pothole-detection/"}
                />
              </center>
            </Container>
          </div>
        );
      }
    }
  }

  render() {
    return (
      <div>
        {this.checkAndGetOutput()}
        {this.checkForContinueSessionButtons()}
        <br />
        <br />
      </div>
    );
  }
}

export default DisplayResults;
