import React, { Component } from "react";
import ProgressSteps from "./progress-steps/scripts/progress_steps.js";
import "bootstrap/dist/css/bootstrap.min.css";
import axios from "axios";
import NotFound from "./not-found/scripts/not_found.js";
import AlertWithBadge from './display-result/alert-with-badge/scripts/alert_with_badge.js';

class App extends Component {

componentDidMount() {
    var self = this;
    if (this.props.match.params.id) {
      axios
        .get(
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/validateSession.php?session_folder_name=" +
            this.props.match.params.id
        )
        .then(function(response) {
          if (response.data === "Valid") {
            self.setState({
              route_session_id: self.props.match.params.id,
              access_granted: true,
              existing_session: true
            });
          } else {
            self.setState({
              route_session_id: "Invalid Access Entries",
              invalid_access: true
            });
          }
        });
    } else {
      this.setState({
        route_session_id: Math.round(Math.random() * 1000) * Date.now(),
        access_granted: true,
        new_session: true
      });
    }
  }

  render() {
    return (
      <div className="App">
        {this.state &&
          this.state.route_session_id &&
          this.state.access_granted &&
          this.state.new_session && (
            <div>
              <center>
                <AlertWithBadge
                  color={"primary"}
                  alert_message={"New Session Created"}
                  _session={this.state.route_session_id}
                />
              </center>
              <ProgressSteps route_session_id={this.state.route_session_id} />
            </div>
          )}
        {this.state &&
          this.state.route_session_id &&
          this.state.access_granted &&
          this.state.existing_session && (
            <div>
              <center>
                <AlertWithBadge
                  color={"secondary"}
                  alert_message={"Existing Session"}
                  _session={this.state.route_session_id}
                />
              </center>
              <ProgressSteps route_session_id={this.state.route_session_id} />
            </div>
          )}
        {this.state &&
          this.state.route_session_id &&
          this.state.invalid_access && <NotFound _session={this.state.route_session_id} />}
      </div>
    );
  }
}

export default App;
