import React, { Component } from "react";
import axios from "axios";
import { Alert } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import ProcessTerminal from "./../../process-terminal/scripts/process_terminal.js";

const API_LINK =
  "http://ec2-18-207-184-85.compute-1.amazonaws.com/pothole_detection/darkflow/process.php?session_folder_name=";

class ProcessFiles extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // Set initial files
      _session_: API_LINK + props.session,
      _session: props.session,
      response: "",
      output_generated_label: "",
      output_text_file_link: "",
      output_text_file_label: "",
      loading_gif_state: "inline",
      enableNext: props.enableNext,
      media_processed_label: "",
      alert_display: "none"
    };
    var self = this;
    axios.get(this.state._session_).then(function(response) {
      self.setState({
        loading_state: "none",
        response: "\n" + response.data + "\n",
        output_generated_label: "Output Text File Generated\n",
        output_text_file_label: "Output Text File",
        output_text_file_link:
          "http://ec2-18-207-184-85.compute-1.amazonaws.com/filepond-api/uploads/" +
          self.state._session +
          "/out/" +
          self.state._session +
          ".txt",
        media_processed_label: "Media File(s) have been Processed!",
        alert_display: "block"
      });
      self.state.enableNext();
    });
  }

  render() {
    return (
      <div>
        <Alert style={{ display: this.state.alert_display }} color="secondary">
          {this.state.media_processed_label}
        </Alert>
        <ProcessTerminal
          loading_state={this.state.loading_state}
          response={this.state.response}
          alert_display={this.state.alert_display}
          alert_label={this.state.output_generated_label}
          output_text_file_link={this.state.output_text_file_link}
          output_text_file_label={this.state.output_text_file_label}
        />
      </div>
    );
  }
}
export default ProcessFiles;
