import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import DisplayResults from './display-result/generate-results/scripts/generate.js';
import NotFound from './not-found/scripts/not_found.js'
import { Route, HashRouter as Router, Switch } from 'react-router-dom'
import * as serviceWorker from './serviceWorker';
import Header from "./header/scripts/header.js";
import Footer from "./footer/scripts/footer.js";

const routing = (
  <Router basename={process.env.PUBLIC_URL}>
    <div>
      <Header/>
      <Switch>
      <Route exact path={"/"} component={App} />
      <Route exact path={"/:id"} component={App} />
      <Route exact path={"/display/:id"} component={DisplayResults} />
      <Route component={NotFound} />
      </Switch>
      <Footer/>
    </div>
  </Router>
)

ReactDOM.render(routing, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
