import React, { Component } from "react";
import Image from "react-image-resizer";

class MediaComponent extends Component {
  getInnerJSX() {
    if (this.props.data.type === "image") {
      console.log(this.props.data.type);
      return (
        <center>
          <div
            style={{
              border: "2px solid #666666",
              backgroundImage:
                "url('https://www.northridgenow.org/wp-content/uploads/2017/09/color-gradient-wallpaper-4.jpg')"
            }}
            width={365}
            height={365}
          >
            <Image
              src={this.props.data.src}
              width={360}
              height={360}
              alt={this.props.data.altText}
            />
          </div>
        </center>
      );
    } else if (this.props.data.type === "video") {
      return (
        <center>
          <div
            style={{
              border: "2px solid #666666",
              backgroundImage:
                "url('https://www.northridgenow.org/wp-content/uploads/2017/09/color-gradient-wallpaper-4.jpg')"
            }}
            width={365}
            height={365}
          >
            <video
              key={this.props.data}
              controls
              style={{ verticalAlign: "middle" }}
            >
              <source src={this.props.data.src} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
          </div>
        </center>
      );
    }
  }
  render() {
    return <div>{this.getInnerJSX()}</div>;
  }
}

export default MediaComponent;
