import React from "react";
import NilssongamesSnake from "nilssongames-snake";
import { Alert } from "reactstrap";

const Game = props => {
  if (window.innerWidth >= 992) {
    return (
      <div>
        <Alert color="primary">
          Enjoy this game, for as long as you want :), <br /> desktop users
          press enter to start, and arrow keys to play!
        </Alert>
        <br />
        <NilssongamesSnake style={{ width: "50px", height: "100px" }} />
      </div>
    );
  }
  else {
    return (
      <div>
      </div>
    )
  }
};

export default Game;
